import {MARKDOWN_FETCH_SUCCESS, MARKDOWN_LIST_FETCH_SUCCESS, PATH_CHANGE} from "../actions/actionTypes"

// 每个reducer都应该有一个initial state

const initialState = {
  md: {},
  markdownList: null,
};

const markdownReducer = (state = initialState, action) => {

  switch (action.type) {
    case MARKDOWN_FETCH_SUCCESS:
      return Object.assign({}, state, {
        md: action.edit ? {} : action.md
      });
    case MARKDOWN_LIST_FETCH_SUCCESS:
      return Object.assign({}, state, {markdownList: action.markdownList});
    case PATH_CHANGE:// 路径发生变化时，将原行的文章内容置空
      return Object.assign({}, state, {md: {}});
  }

  return state;
};

export default markdownReducer;
