// 用来combine reducers
import {combineReducers} from "redux"
import markdownReducer from "./markdownReducer"
import notificationReducer from "./notificationReducer"
import userReducer from "./userReducer"
import editorReducer from "./editorReducer"
import navReducer from "./navReducer"
import searchReducer from "./searchReducer"
import tags from "./tagsReducer"
import books from "./booksReducer"
import theme from "./themeReducer"
import counter from "./counterReducer"
import licaiReducer from "./licaiReducer"
import licaiDetailReducer from "./licaiDetailReducer"
import mybatisSelect from "./mybatisReducer"
import sqlLogs from "./sqlLogsReducer"


const reducers = combineReducers({
  markdownState: markdownReducer,
  notificationState: notificationReducer,
  user: userReducer,
  editorState: editorReducer,
  navState: navReducer,
  search: searchReducer,
  tags,
  books,
  theme,
  counter,
  licai: licaiReducer,
  licaiDetail: licaiDetailReducer,
  mybatisSelect,
  sqlLogs
});

export default reducers;
