import markdownit from "markdown-it"
import md_checkbox from './md-tasklist'
import markdownitFootnote from "markdown-it-footnote"
import md_emoji from "markdown-it-emoji"
import katex from 'markdown-it-katex'
import twemoji from "twemoji"
import hljs from "highlight.js"

import md_tags from './markdown-it-tags';

/**
 * markdown-it 配置
 */

hljs.configure({
  classPrefix: 'hljs-'
});

export const highlight = hljs.highlight;

const md = markdownit({
  html: true,
  highlight: function (code, lang) {

    if (lang && hljs.getLanguage(lang)) {
      try {
        return hljs.highlight(lang, code).value;
      } catch (e) {
        console.warn(e);
      }
    }
    return '';
  }
})
  .use(md_tags, {classes: ['tags', 'tag'], prefix: 'tags: ', suffix: ''})
  .use(md_emoji)
  .use(katex)
  .use(md_checkbox, {inputClass: 'is-checkradio'})
  .use(markdownitFootnote);

md.renderer.rules.emoji = function (token, idx) {
  return twemoji.parse(token[idx].content);
};

export default md;
