import {TABLE_PASTE, TAB_CHANGE} from "../actions/mybatisActions"
import {highlight} from "../lib/md"
import {analyze, java, columns, resultMap, constructorArgs, results, selects, criteria, misc} from "../lib/mybatis"


const tabs = ['Model', 'columns', 'resultMap', '@ConstructorArgs', '@Results', '@Select', 'Criteria', 'Misc'];
const tabsHandler = [
  {func: java, type: 'java'},
  {func: columns, type: 'xml'},
  {func: resultMap, type: 'xml'},
  {func: constructorArgs, type: 'java'},
  {func: results, type: 'java'},
  {func: selects, type: 'java'},
  {func: criteria, type: 'java'},
  {func: misc, type: 'sql'},
];

const initialState = {
  tabs,
  tabIndex: 0,
  sql: null,// 对表创建语句highlight后的结果
  table: null,// 粘贴的表创建语句
  tableName: null,
  fields: [],
  tabData: {},
  err: null,// 解析table出错时使用这个
};


export default (state = initialState, action) => {
  let newState;
  switch (action.type) {
    case TABLE_PASTE:
      newState = Object.assign({}, state, analyze(action.table, high), {tabData: {}});
      break;
    case TAB_CHANGE:
      newState = Object.assign({}, state, {tabIndex: action.tabIndex});
      break;
  }

  if (newState) {
    const {tableName, modelName, tabIndex, tabData, fields} = newState;
    if (!tabData[tabIndex] && fields.length) {
      const {func, type} = tabsHandler[tabIndex];
      tabData[tabIndex] = highlight(type, func({tableName, modelName, fields})).value;
    }
    return newState;
  }

  return state;
}

function high(r) {
  r.sql = highlight('sql', r.table).value;
  return r;
}
