export const LICAI_LIST_FETCHED = 'LICAI_LIST_FETCHED';

export const LICAI_FETCHED = 'LICAI_FETCHED';

export const INPUT_CHANGED = 'INPUT_CHANGED';

export const LICAI_FILTER = 'LICAI_FILTER';


export const licaiFetched = (item) => {
  return {
    type: LICAI_FETCHED,
    item
  }
};

export const licaiListFetched = (list) => {
  return {
    type: LICAI_LIST_FETCHED,
    list: list
  }
};

export const inputChanged = (payload) => {

  return {
    type: INPUT_CHANGED,
    payload
  }
};

export const filterLicai = () => {
  return {
    type: LICAI_FILTER
  }
};

