// tags (== tag1 tag2 ==)

export default function tags(md, opts) {
  opts = opts || {};

  const [outer = '', inner = ''] = opts.classes || [];
  const {prefix = '== ', suffix = ' ==='} = opts;
  const start = prefix.length, end = suffix.length;
  const min_length = start + end;
  const separator = /[,\s]+/;

  md.block.ruler.before('paragraph', 'tags', function tags_rule(state, startLine) {

    let token, pos = state.bMarks[startLine] + state.tShift[startLine], max = state.eMarks[startLine];

    // if it's indented more than 3 spaces, it should be a code block
    if (state.sCount[startLine] - state.blkIndent >= 4) {
      return false;
    }

    const text = state.src.slice(pos, max);
    if (text.length <= min_length || !text.startsWith(prefix) || !text.endsWith(suffix)) {
      return false;
    }
    const tag_arr = text.slice(start, end ? -end : void 0).split(separator);

    state.line = startLine + 1;

    token = state.push('paragraph_open', 'div', 1);
    token.attrSet('class', outer);
    token.markup = prefix;
    token.map = [startLine, state.line];

    token = state.push('inline', '', 0);
    token.map = [startLine, state.line];
    const children = token.children = [];

    for (let i = 0; i < tag_arr.length; i++) {
      token = new state.Token('span_open', 'span', 1);
      token.attrSet('class', inner);
      children.push(token);
      token = new state.Token('text', '', 0);
      token.content = tag_arr[i].trim();
      children.push(token);
      token = new state.Token('span_close', 'span', -1);
      children.push(token);
    }


    token = state.push('paragraph_close', 'div', -1);
    token.markup = suffix;
    return true;
  });
}
