export const TABLE_PASTE = 'TABLE_PASTE';
export const TAB_CHANGE = 'mybatis/tab_change';
export const LOGS_PASTE = 'LOGS_PASTE';

export function pasteTable(table) {
  return {
    type: TABLE_PASTE,
    table
  }
}

export function changeTab(tabIndex) {
  return {
    type: TAB_CHANGE,
    tabIndex
  }
}

/**
 * 粘贴 mybatis sql日志
 */
export function pasteLogs(logs) {
  return {
    type: LOGS_PASTE,
    logs
  }
}
