export const BOOKS_LOADED = 'BOOKS_LOADED';
export const BOOKS_EDIT = 'BOOKS_EDIT';
export const FIELD_CHANGED = 'FIELD_CHANGED';
export const BOOKS_SAVED = 'BOOKS_SAVED';


export function booksLoaded(books) {
  return {
    type: BOOKS_LOADED,
    data: books
  }
}

/**
 * 编辑给定id的书籍信息
 */
export function editBooks(editing) {
  return {
    type: BOOKS_EDIT,
    editing
  }
}

export function changeField({field, value, idx, type}) {
  return {
    type: FIELD_CHANGED,
    data: {
      field,
      value,
      type,
      idx
    }
  }
}

export function bookSaved(book) {
  return {
    type: BOOKS_SAVED,
    book
  }
}
