import {LICAI_LIST_FETCHED, LICAI_FILTER} from "../actions/licaiActions"

const initialState = {
  list: [],
  detail: {},
  filter: false,
};

export default (state = initialState, action) => {
  switch (action.type) {

    case LICAI_LIST_FETCHED:
      return {...state, ...{list: sort(action.list), detail: {}}};
    case LICAI_FILTER: {
      return {...state, ...{filter: !state.filter}};
    }
  }

  return state;
}

function sort(list = []) {
  return list.sort((a, b) => {
    if (a.app === b.app) {
      return a.end > b.end ? -1 : 1;
    }
    return a.app > b.app ? 1 : -1;
  })
}
