import * as types from "../actions/actionTypes"
import date from "../lib/date"

const {default: example} = require('example.md');

// 编辑器属性处理
const initialState = {
  id: null,
  title: '',
  content: example.replace('2016-10-17', date()),
  owner: null,
  mouseEntered: null// 鼠标所在处
};


export default (state = initialState, action) => {

  switch (action.type) {
    case types.MARKDOWN_FETCH_SUCCESS:// 当文章从服务器加载出来后，需要强制编辑器更新
      return Object.assign({}, state, action.edit ? action.md : {}, {forceUpdate: true});

    case types.MARKDOWN_CHANGED:// 用户改动了文章，编辑器不用再强制更新
      return Object.assign({}, state, {
        content: action.content,
        forceUpdate: false
      });

    case types.MARKDOWN_SAVED:// 文章保存成功后, 使用最新的文单内容(为了使用其md)
      return Object.assign({}, state, action.md);

    case types.PATH_CHANGE: // 路径改变后，重置文章数据
      return initialState;

    case types.MOUSE_ENTERED:
      return {...state, ...{mouseEntered: action.mouseEntered}};
  }
  return state;
}
