import {BOOKS_LOADED, BOOKS_EDIT, FIELD_CHANGED} from "../actions/bookActions"

const initialState = {
  editing: -1,
  data: []
};


const bookFormReducer = (book, data) => {

  if (data.idx != null) {
    const links = book.links.concat([]);
    const link = links[data.idx];
    if (data.field === 'title') {
      link.title = data.value;
    }
    if (data.field === 'url') {
      link.url = data.value;
    }
    return Object.assign({}, book, {links});

  }
  switch (data.field) {
    case 'title': {
      return Object.assign({}, book, {title: data.value});
    }
    case 'desc': {
      return Object.assign({}, book, {desc: data.value});
    }
    case 'img': {
      return Object.assign({}, book, {img: data.value});
    }
  }

  return book;
};

export default (state = initialState, action) => {
  switch (action.type) {
    case BOOKS_LOADED: {
      return {data: action.data.concat(appendable()), editing: -1};
    }
    case BOOKS_EDIT: {
      let {links} = state.data[action.editing];
      if (links.length < 3) {
        links = links.concat([{}, {}, {}]).slice(0, 3);
      }
      const newState = Object.assign({}, state, {editing: action.editing});
      newState.data[action.editing].links = links;
      return newState;
    }
    case FIELD_CHANGED: {
      if (action.data.type === 'book') {
        const data = state.data.concat([]);
        data[state.editing] = bookFormReducer(data[state.editing], action.data);
        return Object.assign({}, state, {data});
      }
    }
  }

  return state;
}

function appendable() {
  return {
    img: 'https://img14.360buyimg.com/n1/jfs/t1759/57/1234940139/123217/182b5287/55e3ffb2N2c890d40.jpg',
    title: 'android 开发艺术探索',
    desc: '模板文件，随意编辑',
    links: [
      {url: '', title: '相关链接'},
      {url: '', title: '相关链接2'},
    ]
  }
}
