import React from "react"
import {NavLink, withRouter} from "react-router-dom"
import {connect} from "react-redux"
import {saveMarkdownAsync, getMarkdownList} from "../../api/markdownApi"
import {changeTheme} from "../../actions/themeActions"
import {pathChanged} from "../../actions/userActions"

// 导航菜单布局
class NavLayout extends React.Component {

  componentDidMount() {
    document.documentElement.dataset.theme = this.props.theme;
    this.props.pathChanged(/^\/write/.test(this.props.location.pathname));
    this.props.history.listen(location => {
      this.props.pathChanged(/^\/write/.test(location.pathname));
      if (typeof ga !== 'undefined') {
        // https://developers.google.com/analytics/devguides/collection/analyticsjs/single-page-applications
        ga('set', 'page', location.pathname);
        ga('send', 'pageview');
      }
    });
  }

  componentWillReceiveProps(nextProps) {
    const {theme} = nextProps;
    if (this.props.theme !== theme) {
      document.documentElement.dataset.theme = theme;
    }
  }

  render() {
    const {showSave, hasUser, changeTheme} = this.props;
    return <nav className="navbar is-fixed-top">
      <div className="container">
        <div className="navbar-brand">
          <NavLink className="navbar-item site" to="/" onClick={() => this.props.getMarkdownList('')}>
            <strong>Promise</strong>
          </NavLink>
        </div>
        <div className="navbar-menu">
          <div className="navbar-start">

            <NavLink to="/" exact className="navbar-item" activeClassName="is-active">Home</NavLink>
            <NavLink to="/hot" exact className="navbar-item" activeClassName="is-active">Hot</NavLink>
            <NavLink to="/books" className="navbar-item" activeClassName="is-active">Books</NavLink>
            <NavLink to="/finance" className="navbar-item" activeClassName="is-active">Finance</NavLink>
            {
              !showSave && hasUser &&
              <NavLink to="/write" className="navbar-item write" activeClassName="is-active">Write</NavLink>
            }

            <NavLink to="/article/12b91b37-ae77-4496-b720-d00664d2b508" className="navbar-item"
                     activeClassName="is-active">About</NavLink>

            {
              showSave && hasUser && (
                <span className="navbar-item write">
                            <a className="button is-info" onClick={this.props.save}>
                                Save
                            </a>
                        </span>
              )
            }

          </div>
          <div className="navbar-end">
            <a className="navbar-item" href="https://github.com/Youmoo">
                      <span className="icon">
                        <i className="fa fa-github"/>
                      </span>
            </a>
            <a className="navbar-item" href="https://twitter.com/Youmoooo">
                      <span className="icon">
                        <i className="fa fa-twitter"/>
                      </span>
            </a>
            <a className="navbar-item" onClick={changeTheme}>
                      <span className="icon">
                        <i className="fa fa-hand-o-up"/>
                      </span>
            </a>
          </div>
        </div>

      </div>
    </nav>
  }
}

const mapStateToMap = store => {
  const state = {
    showSave: store.navState.showSave,
    hasUser: !!store.user.id,
    theme: store.theme
  };

  if (store.editorState.id !== null) {
    // 只允许编辑自己的文章
    if (store.editorState.owner !== store.user.id) {
      state.showSave = false;
    }
  }
  return state;
};

// 如果mapDispatchToProps是一个对象，则对象属性名是组件的属性名，对象的属性值
const mapDispatchToProps = {
  save: saveMarkdownAsync,
  getMarkdownList,
  changeTheme,
  pathChanged
};

export default withRouter(connect(mapStateToMap, mapDispatchToProps)(NavLayout));
