import * as types from "./actionTypes"

export function getMarkdownListSuccess(markdownList) {
  return {
    type: types.MARKDOWN_LIST_FETCH_SUCCESS,
    markdownList: markdownList
  }
}

export function markdownFetched(md, edit = false) {
  return {
    type: types.MARKDOWN_FETCH_SUCCESS,
    md: md,
    edit
  }
}

/**
 * 更改文章内容
 *
 * @param input 文章内容
 */
export function changeMarkdown(input) {
  return {
    type: types.MARKDOWN_CHANGED,
    content: input
  }
}

export function markdownSaved(md) {
  return {
    type: types.MARKDOWN_SAVED,
    md
  }
}

export function changeSearch(search) {
  return {
    type: types.SEARCH_CHANGE,
    search
  }
}


export function counterFetched(id, counter = 1) {
  return {
    type: types.COUNTER_FETCHED,
    id,
    counter
  }
}
