import {SWITCH_THEME} from "../actions/themeActions"

const themes = ['default', 'dark', 'green', 'purple', 'black', 'orange'];

const length = themes.length;

let idx = +localStorage.theme || 0;

const initialState = themes[idx % length];

export default (state = initialState, action) => {
  switch (action.type) {
    case SWITCH_THEME: {
      idx = (idx + 1) % length;
      localStorage.theme = idx;
      return themes[idx];
    }
  }

  return state;
}
