import * as markdownActions from "../actions/markdownActions"
import * as notificationActions from "../actions/notificationActions"
import * as actionTypes from "../actions/actionTypes"
import * as licaiActions from "../actions/licaiActions"
import {booksLoaded} from "../actions/bookActions"
import {incrCounter} from "../actions/counterApiInvoker"
import {userFetched} from "../actions/userActions"
import Horizon from "@horizon/client"
import {noop} from "../lib/utils"

let auth = {
  authType: 'unauthenticated'
};


if (process.env.NODE_ENV !== 'production') {
  auth = {...auth, secure: true, host: 'promise.rocks'}
}

let hz = initHz();

function initHz() {
  let hzz = Horizon(auth);

  hzz.onSocketError(err => {
    console.log('socket err: ', err);
    try {
      if (hzz.hasAuthToken()) {
        console.info('Has tokens...');
        // Horizon.clearAuthTokens();
        // location.replace('/');
      }
    } catch (e) {
      console.log('err: ', e);
      // location.replace('/');
    }

  });
  hzz.connect(err => {// see: https://discuss.horizon.io/t/solved-catch-errors-when-the-server-shut-down/482
    console.warn('server err: ', err);
    console.info('reinitialize horizon..');
    hz = initHz();
  });
  return hzz;
}

export function fetchUser() {
  return (dispatch) => {
    // 获取用户
    hz.currentUser().fetch().subscribe(u => {
      console.log('user:', u);
      dispatch(userFetched(u));

    }, err => {
      // Unauthenticated users have no user document
      console.warn('fetch user failed: ', err.message);
    });

  }
}

export function getMarkdownList(q) {

  return (dispatch, getState) => {
    if (q != null) {
      dispatch(markdownActions.changeSearch(q));
    }
    const {search: query} = getState();
    if (!query) {
      hz('md').order('modified', 'descending').limit(20).fetch().subscribe(items => {
        dispatch(markdownActions.getMarkdownListSuccess(items));
      });
    }
    else {
      fetch(`/api/search?q=${encodeURIComponent(query)}`, {
        'mode': 'no-cors'
      })
        .then((res) => {
          return res.json();
        })
        .then(items => {
          dispatch(markdownActions.getMarkdownListSuccess(items));
        })
        .catch(err => {
          throw err;
        })
    }
  }

}

/**
 * 解析markdown的标题
 *
 * @param md 一条完整的md记录
 */
const title = (md) => {
  return /^#\s(.+)/.exec(md.content)[1] || md.title
};

/**
 * 解析markdown的tags
 *
 * @param md 一条完整的md记录
 */
const tags = (md) => {
  const t = /.*tags:\s+(.*)/.exec(md.content);

  if (!t || !t[1]) {
    return md.tags;
  }
  return t[1].split(/\s+/).map(v => v.trim())
};

/**
 * 根据id获取md文章
 *
 * @param id 记录id
 * @param edit 是否是供编辑的
 */
export function getMarkdown(id, edit = false) {
  if (!edit) {
    incrCounter(id);
  }
  return (dispatch) => {

    id && hz('md').find({id: id}).fetch().subscribe(item => {
      dispatch(markdownActions.markdownFetched(item, edit))
    })
  }
}

/**
 * 保存markdown
 */
export function saveMarkdownAsync() {

  return (dispatch, getState) => {

    // 这个太他妈方便了,在nav组件内触发的事件，可以获取editor组件的状态
    const {editorState, user} = getState();

    // 复制一份
    const md = Object.assign({}, editorState);

    if (!md.id) {
      delete md.id
    }
    delete md.mouseEntered;
    delete md.forceUpdate;
    md.owner = user.id;
    md.title = title(md);
    md.tags = tags(md);
    md.modified = new Date();
    md.created = md.created || md.modified;

    hz("md").upsert(md)
      .subscribe(a => {
        console.log('markdown saved:', a);
        dispatch(markdownActions.markdownSaved({
          id: a.id,
          owner: user.id
        }));
        dispatch(notificationActions.showNotification());
        setTimeout(function () {
          dispatch(notificationActions.hideNotification());
        }, 800);
      }, err => {
        console.log(err);
      })
  }
}

export function getTags() {
  return (dispatch, getState) => {
    fetch('/api/tags', {mode: 'no-cors'})
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.TAGS_LOADED,
          data
        })
      })
      .catch(err => {
        console.log(err);
      })
  }
}

/*****************books start*********************/

// 加载书籍
export function getBooks() {
  return (dispatch) => {

    hz('book').order('modified', 'descending').limit(200).fetch().subscribe(items => {
      dispatch(booksLoaded(items));
    });
  }
}

export function saveBook() {

  return (dispatch, getState) => {

    // 这个太他妈方便了,在nav组件内触发的事件，可以获取editor组件的状态
    const {books: {data, editing}, user: user} = getState();

    // 复制一份
    const book = Object.assign({}, data[editing]);

    if (!book.id) {
      delete book.id
    }
    book.owner = user.id;
    book.modified = new Date();
    book.created = book.created || book.modified;

    hz("book").upsert(book)
      .subscribe(a => {

        notificationActions.autocloseNotification(dispatch);
        getBooks()(dispatch);
      }, err => {
        console.warn('save book err: ', err);
      })
  }
}

export function deleteBook(id, cb = noop) {
  hz('book').remove(id).subscribe(cb)
}

/*****************books end*********************/

/*****************理财start*********************/

export function licaiList() {
  return (dispatch, getState) => {
    const {user: user} = getState();
    user.id && hz('licai').order('app').findAll({owner: user.id}).fetch().subscribe(items => {

      dispatch(licaiActions.licaiListFetched(items))
    })
  }

}

export function licaiDetail(id) {
  return (dispatch) => {
    hz('licai').find(id).fetch().subscribe(item => {
      dispatch(licaiActions.licaiFetched(item));
    }, console.warn)
  }
}

export function saveLicai(cb = noop) {
  return (dispatch, getState) => {
    const {user, licaiDetail: data} = getState();
    data.owner = user.id;
    hz('licai').store(data).subscribe(cb, err => console.warn('save licai error:', err));
  }
}

export function deleteLicai(id, cb = noop) {
  hz('licai').remove(id).subscribe(cb, err => console.warn('delete licai err: ', err));
}

/*****************理财end*********************/
