import {PATH_CHANGE} from "../actions/actionTypes"

// 是否展示保存按钮
const initialState = {
  showSave: false
};

export default (state = initialState, action) => {

  switch (action.type) {

    case PATH_CHANGE:
      return Object.assign({}, state, {
        showSave: action.data
      });
  }

  return state;
}
