import React from "react"
import {connect} from "react-redux"
import {fetchUser} from "../../api/markdownApi"
import PropTypes from 'prop-types';
import {Redirect, Route, Switch} from "react-router-dom"
import NavLayout from "../layouts/NavLayout"
import Notification from "../views/Notification"
import Bundle from '../Bundle'


class MainContainer extends React.Component {
  componentDidMount() {
    this.props.fetchUser();
  }

  render() {
    const {userId} = this.props;
    return <React.Fragment>

      <NavLayout/>
      <section className="section">
        <Switch>
          <Route exact path="/" component={ArticleListContainer}/>
          <Route exact path="/hot" component={HotContainer}/>
          <Route exact path="/tags" component={TagsContainer}/>
          <Route exact path="/books" component={BooksContainer}/>
          <Route exact path="/finance" render={(props) => {
            if (userId) {// 有用户才加载
              return <LicaiListContainer/>
            }
            return <Loading tip={'Login required.'}/>
          }
          }/>
          <Route exact path="/finance/form/:id?" component={LicaiFormContainer}/>
          <Route exact path="/article/:id" component={ArticleContainer}/>
          <Route path="/write/:id?" component={EditorContainer}/>
          <Route path="/mybatis/sql" component={MyBatisSQLContainer}/>
          <Route path="/mybatis" component={MyBatisContainer}/>
          <Route path="/model_to_table" component={ModelToMarkdownTableContainer}/>
          <Route path="/d/:sub" component={DynamicContainer}/>
          {/*404转主页*/}
          <Redirect to="/"/>
        </Switch>
      </section>
      <Notification {...this.props.notification}/>
    </React.Fragment>
  }
}

MainContainer.propTypes = {
  notification: PropTypes.shape({
    show: PropTypes.bool.isRequired,
  }).isRequired,
  fetchUser: PropTypes.func.isRequired
};


const mapStateToProps = (state) => {
  return {
    notification: state.notificationState,
    userId: state.user.id,
  }
};

const mapDispatchToProps = {
  fetchUser
};

const Loading = ({tip = 'Loading...'} = {}) => (
  <main className="container">
    <div className="bundle_loading">{tip}</div>
  </main>
);

const ArticleListContainer = (props) => (
  <Bundle load={require('bundle-loader?lazy&name=article_list!../containers/ArticleListContainer')}>
    {(Comp) => (Comp
        ? <Comp {...props}/>
        : <Loading/>
    )}
  </Bundle>
);

const HotContainer = (props) => (
  <Bundle load={require('bundle-loader?lazy&name=article_list!../containers/HotContainer')}>
    {(Comp) => (Comp
        ? <Comp {...props}/>
        : <Loading/>
    )}
  </Bundle>
);

const TagsContainer = (props) => (
  <Bundle load={require('bundle-loader?lazy&name=tags!../containers/TagsContainer')}>
    {(Comp) => (Comp
        ? <Comp {...props}/>
        : <Loading/>
    )}
  </Bundle>
);

const BooksContainer = (props) => (
  <Bundle load={require('bundle-loader?lazy&name=books!../containers/BooksContainer')}>
    {(Comp) => (Comp
        ? <Comp {...props}/>
        : <Loading/>
    )}
  </Bundle>
);

const LicaiListContainer = (props) => (
  <Bundle load={require('bundle-loader?lazy&name=licai_list!../containers/LicaiListContainer')}>
    {(Comp) => (Comp
        ? <Comp {...props}/>
        : <Loading/>
    )}
  </Bundle>
);

const LicaiFormContainer = (props) => (
  <Bundle load={require('bundle-loader?lazy&name=licai_form!../containers/LicaiFormContainer')}>
    {(Comp) => (Comp
        ? <Comp {...props}/>
        : <Loading/>
    )}
  </Bundle>
);

const EditorContainer = (props) => (
  <Bundle load={require('bundle-loader?lazy&name=editor!../containers/EditorContainer')}>
    {(Comp) => (Comp
        ? <Comp {...props}/>
        : <Loading/>
    )}
  </Bundle>
);

const ArticleContainer = (props) => (
  <Bundle load={require('bundle-loader?lazy&name=article!../containers/ArticleContainer')}>
    {(Comp) => (Comp
        ? <Comp {...props}/>
        : <Loading/>
    )}
  </Bundle>
);

const MyBatisContainer = (props) => (
  <Bundle load={require('bundle-loader?lazy&name=mybatis!../containers/MyBatis')}>
    {(Comp) => (Comp
        ? <Comp {...props}/>
        : <Loading/>
    )}
  </Bundle>
);

const MyBatisSQLContainer = (props) => (
  <Bundle load={require('bundle-loader?lazy&name=mybatis_sql!../containers/MyBatisSQL')}>
    {(Comp) => (Comp
        ? <Comp {...props}/>
        : <Loading/>
    )}
  </Bundle>
);

const ModelToMarkdownTableContainer = (props) => (
  <Bundle load={require('bundle-loader?lazy&name=mybatis_sql!../containers/ModelToMarkdownTable')}>
    {(Comp) => (Comp
        ? <Comp {...props}/>
        : <Loading/>
    )}
  </Bundle>
);

const DynamicContainer = (props) => {
  const {sub} = props.match.params;
  const component = sub;
  // 对于动态组件，其chunkname一定要包裹在数组中,webpack会将该文件夹下的每个文件都打成单个包
  // https://github.com/webpack/webpack/issues/4807#issuecomment-300443876
  return <Bundle
    load={require(/* webpackChunkName: "[request]" */`bundle-loader?lazy!../containers/dynamic/${component}`)}>
    {(Comp) => (Comp
        ? <Comp {...props}/>
        : <Loading/>
    )}
  </Bundle>
};
export default connect(mapStateToProps, mapDispatchToProps)(MainContainer);
