import {LICAI_LIST_FETCHED, LICAI_FETCHED, INPUT_CHANGED} from "../actions/licaiActions"

const initialState = {};

export default (state = initialState, action) => {
  switch (action.type) {

    case LICAI_LIST_FETCHED:
      return initialState;
    case LICAI_FETCHED:
      return action.item;
    case INPUT_CHANGED:
      return Object.assign({}, state, action.payload);
  }

  return state;
}
