import {highlight} from './md'

export function parse(logs) {
  return /.*Preparing: ([^]+?)\n.*Parameters: ([^]+?)\n.*(?:Total|Updates): (\d+)/g.exec(logs).slice(1);
}

export function transform(logs) {
  const [sql, args, total] = parse(logs);
  return {sql, args, total, parsed: combine(sql, args)};
}

export function parseArgs(args, append = true) {
  if (append) {
    args += ', o(k)'
  }

  const reg = /([^]*?)\((\w+)\)(, )?/g;
  let group;
  const results = [];
  while (group = reg.exec(args)) {
    const [full, val, type,] = group;
    if (full.startsWith('null,')) {
      results.push({
        val: 'null',
        type: 'Null'
      });
      const r = parseArgs(full.substring(6), false);
      results.push(...r);
    } else {
      results.push({
        val,
        type
      });
    }

  }
  return append ? results.slice(0, -1) : results;
}

export function combine(sql, args) {
  args = parseArgs(args);
  let i = 0;
  while (sql.includes('?')) {
    // 注意null值的处理
    sql = sql.replace('?', args[i].type === 'Null' ? 'null' : `'${args[i].val}'`);
    i++;
  }
  return highlight('sql', sql).value;
}
