import {splitEvery as chunk} from "ramda"

/**
 * CREATE TABLE `book` (
 `id` int(11) NOT NULL AUTO_INCREMENT,
 `title` varchar(45) COLLATE utf8mb4_unicode_ci NOT NULL,
 `genre` varchar(45) COLLATE utf8mb4_unicode_ci DEFAULT NULL,
 PRIMARY KEY (`id`)
 ) ENGINE=InnoDB AUTO_INCREMENT=6 DEFAULT CHARSET=utf8mb4 COLLATE=utf8mb4_unicode_ci
 */
export const mappings = {
  varchar: 'String',
  int: 'Integer',
  bigint: 'Long',
  decimal: 'BigDecimal',
  datetime: 'Date',
  date: 'LocalDate',
  year: 'Year',
  time: 'LocalTime',
  float: 'BigDecimal',
  double: 'BigDecimal',
};


export function analyzeFields(input) {
  if (/\s/.test(input)) {
    return {
      err: '格式不正确。示例=> id:int,name:varchar',
      table: input
    }
  }
  const reg = /(\w+):(\w+)/gm;
  let result;
  const fields = [];
  while (result = reg.exec(input)) {
    const [, column, jdbcType] = result;
    fields.push({column, jdbcType, property: columnToProperty(column), javaType: mappings[jdbcType] || 'String'});
  }
  return {
    table: input,
    sql: input,
    tableName: 'whatever',
    modelName: 'Whatever',
    err: null,
    fields
  }
}


export function analyze(table, postProcess = p => p) {
  if (!/\s*create table /i.test(table)) {
    return analyzeFields(table);
  }
  const [, tableName] = /`(.+)`/.exec(table);
  const reg = /^\s*`(.+)`\s([\w]+)(\(?.+\))?\s/gm;
  let result;
  const fields = [];
  while (result = reg.exec(table)) {
    const [, column, jdbcType] = result;
    fields.push({column, jdbcType, property: columnToProperty(column), javaType: mappings[jdbcType] || 'String'});
  }
  const r = {
    table,
    tableName,
    modelName: tableName[0].toUpperCase() + tableName.substring(1),
    err: null,
    fields
  };
  return postProcess(r);
}

/**
 * 将mysql列名改为java属性名
 */
export function columnToProperty(column) {
  return column.replace(/_\w/g, a => a[1].toUpperCase());
}

/**
 *@ConstructorArgs({
      @Arg(column = "AUTHOR_ID", javaType = Integer.class),
      @Arg(column = "AUTHOR_USERNAME", javaType = String.class),
      @Arg(column = "AUTHOR_PASSWORD", javaType = String.class),
      @Arg(column = "AUTHOR_EMAIL", javaType = String.class),
      @Arg(column = "AUTHOR_BIO", javaType = String.class),
      @Arg(column = "AUTHOR_SECTION", javaType = Section.class)
  })
 */
export function constructorArgs({fields}) {
  return (
    `@ConstructorArgs({
${fields.map(v => `  @Arg(column = "${v.column}", javaType = ${v.javaType}.class)`).join(',\n')}
})`
  )
}

/**
 *@Results({
      @Result(property = "username", column = "AUTHOR_USERNAME"),
      @Result(property = "password", column = "AUTHOR_PASSWORD"),
      @Result(property = "email", column = "AUTHOR_EMAIL"),
      @Result(property = "bio", column = "AUTHOR_BIO")
  })
 */
export function results({fields}) {
  return (
    `@Results({
${fields.map(v => `  @Result(property = "${v.property}", column = "${v.column}")`).join(',\n')}
})`
  )
}

/**
 *   @Select({
      "SELECT ",
      "  ID as AUTHOR_ID,",
      "  USERNAME as AUTHOR_USERNAME,",
      "  PASSWORD as AUTHOR_PASSWORD,",
      "  EMAIL as AUTHOR_EMAIL,",
      "  BIO as AUTHOR_BIO",
      "FROM AUTHOR WHERE ID = #{id}"})
 */
export function selects({tableName, fields}) {
  return (
    `@Select({
  "select",
${fields.map(v => `    "${v.column}`).join(',",\n') + `",`}
  "from ${tableName}"})
`)
}

/**
 *         public Criteria andCustomerIdEqualTo(Integer value) {
            addCriterion("customer_id =", value, "customerId");
            return (Criteria) this;
        }
 */
export function criteria({tableName, fields}) {
  return fields.map(v => {
    const camel = v.property[0].toUpperCase() + v.property.substring(1);
    return (
      `  public Criteria and${camel}EqualTo(${v.javaType} value){
    addCriterion("${v.column} =", value, "${v.property}");
    return (Criteria) this;
  }

  public Criteria and${camel}NotEqualTo(${v.javaType} value) {
    addCriterion("${v.column} <>", value, "${v.property}");
    return (Criteria) this;
  }

  public Criteria and${camel}In(Collection<${v.javaType}> values) {
    addCriterion("${v.column} in", values, "${v.property}");
    return (Criteria) this;
  }

  public Criteria and${camel}NotIn(Collection<${v.javaType}> values) {
    addCriterion("${v.column} not in", values, "${v.property}");
    return (Criteria) this;
  }

  public Criteria and${camel}LessThan(${v.javaType} value) {
    addCriterion("${v.column} <", value, "${v.property}");
    return (Criteria) this;
  }

  public Criteria and${camel}ThanOrEqualTo(${v.javaType} value) {
    addCriterion("${v.column} <=", value, "${v.property}");
    return (Criteria) this;
  }
  public Criteria and${camel}GreaterThan(${v.javaType} value) {
    addCriterion("${v.column} >", value, "${v.property}");
    return (Criteria) this;
  }

  public Criteria and${camel}GreaterThanOrEqualTo(Long value) {
    addCriterion("${v.column} >=", value, "${v.property}");
    return (Criteria) this;
  }
  
  public Criteria and${camel}IsNull() {
    addCriterion("${v.column} is null");
    return (Criteria) this;
  }

  public Criteria and${camel}IsNotNull() {
    addCriterion("${v.column} is not null");
    return (Criteria) this;
  }
`)
  }).join('\n');
}

/**
 *   <sql id="Base_Column_List" >
 id, code, status, price, discount_price, created, modified, end_time, pay_time, customer_id,
 seller_id, brand_id, receiver, receiver_phone, receiver_address, post_fee, memo,points
 </sql>
 */
export function columns({fields}) {
  return `<sql id="Base_Column_List" >
  ${chunk(5, fields).map(v => v.map(c => c.column).join(',')).join(',\n  ')}
</sql>`
}

/**
 *  <resultMap id="BaseResultMap" type="com.yunejian.yejTb.model.TradeStatusLog" >
 <id column="id" property="id" jdbcType="BIGINT" />
 <result column="trade_code" property="tradeCode" jdbcType="VARCHAR" />
 <result column="created" property="created" jdbcType="TIMESTAMP" />
 <result column="from_status" property="fromStatus" jdbcType="VARCHAR" />
 <result column="to_status" property="toStatus" jdbcType="VARCHAR" />
 </resultMap>
 */
export function resultMap({tableName, fields}) {
  return (
    `<resultMap id="BaseResultMap" type="${tableName}">
${fields.map(v => `  <result column="${v.column}" property="${v.property}"/>`).join('\n')}
</resultMap>`
  )
}

export function java({tableName, modelName, fields}) {

  return (
    `@lombok.Data
public class ${modelName} {
${fields.map(v => `    ${v.javaType} ${v.property}`).join(';\n')};
}
`)
}

/**
 *       <if test="email != null" >
 email = #{email,jdbcType=VARCHAR},
 </if>
 */
export function misc({tableName, fields}) {
  return (
    `update ${tableName} set 
${fields.map(v => `  ${v.column} = #{${v.property}}`).join(',\n')};

update ${tableName}
<set>
${fields.map(v => (
      `  <if test = "${v.property} != null">
    ${v.column} = #{${v.property}},
  </if>`)).join('\n')}
</set>

insert into ${tableName}
(${chunk(5, fields).map(v => v.map(c => c.column).join(',')).join(',\n  ')})
values
(${chunk(5, fields).map(v => v.map(c => `#{${c.property}}`).join(',')).join(',\n  ')})
`
  )
}

export function mapper({tableName, modelName, fields}) {
  return (
    `@Repository
public interface ${modelName}Mapper {

}`
  )
}

export function example({modelName, fields}) {
  return (
    `public class ${modelName}Example {
   
}`
  )
}

export function mapperXml({tableName, modelName, fields}) {
  return (
    `<?xml version="1.0" encoding="UTF-8" ?>
<!DOCTYPE mapper PUBLIC "-//mybatis.org//DTD Mapper 3.0//EN" "http://mybatis.org/dtd/mybatis-3-mapper.dtd" >
<mapper namespace="com.yunejian.yejTb.mapper.TradeStatusLogMapper" >
  ${resultMap({tableName, fields})}
</mapper>
`
  )
}
